import React, { memo, useEffect } from "react"
import EntireFilter from "./components/entire-filter"
import EntireRooms from "./components/entire-rooms"
import EntirePagination from "./components/entire-pagination"
import { EntireWrapper } from "./style"
import { shallowEqual, useDispatch, useSelector } from "react-redux"
import { fetchRoomData } from "@/store/modules/entire"
import { changeHeaderConfigAction } from "../../store/modules/main"

const Entire = memo((props) => {
  const dispatch = useDispatch()
  const { currentPage } = useSelector(
    (state) => ({
      currentPage: state.entire.currentPage
    }),
    shallowEqual
  )

  useEffect(() => {
    dispatch(changeHeaderConfigAction({ isFixed: true }))
    dispatch(fetchRoomData(currentPage))
  }, [currentPage, dispatch])

  return (
    <EntireWrapper>
      <EntireFilter />
      <EntireRooms />
      <EntirePagination />
    </EntireWrapper>
  )
})

export default Entire
