import React, { memo, useCallback } from "react"
import { shallowEqual, useDispatch, useSelector } from "react-redux"
import RoomItem from "@/components/room-item"
import { RoomsWrapper } from "./style"
import { useNavigate } from "react-router-dom"
import { changeCurrentItemAction } from "@/store/modules/detail"

const EntireRooms = memo(() => {
  const dispatch = useDispatch()
  const { roomList, totalCount, isLoading } = useSelector(
    (state) => ({
      roomList: state.entire.roomList,
      totalCount: state.entire.totalCount,
      isLoading: state.entire.isLoading
    }),
    shallowEqual
  )

  const navigate = useNavigate()
  const showMoreDetail = useCallback(
    (item) => {
      dispatch(changeCurrentItemAction(item))
      navigate("/detail")
    },
    [navigate, dispatch]
  )
  return (
    <RoomsWrapper>
      {!!totalCount && <h2 className="title">共{totalCount}多处住宿</h2>}
      <div className="list">
        {roomList.map((item) => (
          <RoomItem
            itemData={item}
            itemWidth="20%"
            key={item._id}
            itemClick={showMoreDetail}
          />
        ))}
      </div>

      {isLoading && <div className="mask"></div>}
    </RoomsWrapper>
  )
})

export default EntireRooms
